.cardContainer {
  background-color: #eee;
  padding: 15px;
  margin: 10px;
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

h1 {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 18px;
}
h2 {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 12px;
}

.image {
  width: 70px;
  height: 70px;
  object-fit: cover;
  border-radius: 50%;
}
.diagnosis {
  color: #1882c4;
}
.diagnosisResult {
  color: #174b99;
  font-weight: 700;
}
.roomNumber {
  margin: 20px 0 5px 0;
}
.link {
  width: 80%;
  color: white;
  background-color: #d62b5e;
  padding: 5px 10px;
  border: none;
  display: flex;
  justify-content: center;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  font-size: 14px;
}
.link:hover {
  cursor: pointer;
  background-color: #8f1d3f;
}

.options {
  position: absolute;
  right: 5px;
  top: 5px;
  font-size: 20px;
}
.options:hover .icon {
  opacity: 0.5;
  cursor: pointer;
}

.actions {
  position: absolute;
  top: 0;
  right: 0;
  text-align: right;
}
.actions button {
  background-color: #d62b5e;
  border: none;
  color: white;
  padding: 5px;
  width: 150px;
}
.actions button:hover {
  background-color: #b4224e;
  cursor: pointer;
}
